
var appKey = "Troy";
var appSecret = "Troy";

function getAppEnvironment() {
	try {
		//debugger;
		let appEnvironment = uni.getStorageSync('appEnvironment');
		if (appEnvironment != null && appEnvironment != "" && typeof appEnvironment != undefined) {
			return appEnvironment;
		} else {
			return 'production';
		}
	} catch (e) {
		//TODO handle the exception
		return 'production';
	}
}

function getServerUrl() {
	let appEnvironment = getAppEnvironment();
	let serverList = uni.getStorageSync('serverList');
	if (appEnvironment === 'production') {
		return 'https://api.pandianapp.com/api';
	} else if (appEnvironment === 'test') {
		return 'https://apidemo.pandianapp.com/api';
	} else if (appEnvironment === 'private') {
		return serverList.privateServer;
	} else {
		return 'https://api.pandianapp.com/api';
	}
}


function getUserInfo() {
	var userInfo = uni.getStorageSync("currentUserInfo");
	if (userInfo != null && userInfo != "" && typeof userInfo != undefined) {
		if (userInfo.expiredDate > new Date()) {
			return userInfo;
		} else {
			uni.removeStorageSync("currentUserInfo");
			return null;
		}
	} else {
		return null;
	}
}

export default {
	appKey,
	appSecret,
	getServerUrl,
	getUserInfo
}
